<template>
  <div class="container">
    <MainHeader />
    <Transition name="slide">
      <div v-if="isSlideLoading"></div>
      <SlideSpot v-else :spots="scenicSpots" />
    </Transition>
    <Transition name="cardList">
      <div v-if="isLoading"></div>
      <div v-else>
        <SpotList :cardList="cardList" />
        <div v-if="cardList.length !== 0" class="btn-section">
          <button
            @click="showMore"
            class="moreBtn btn btn-primary"
            :disabled="isLoading"
          >
            <div
              v-if="isProcessing"
              class="spinner-border spinner-border-sm text-light"
              role="status"
            >
              <span class="visually-hidden">Loading...</span>
            </div>
            <span v-else>更多景點</span>
          </button>
        </div>
      </div>
    </Transition>
  </div>
</template>

<script>
import MainHeader from "../components/MainHead.vue";
import SlideSpot from "./../components/SlideSpot.vue";
import SpotList from "./../components/SpotList.vue";
import spotAPI from "./../apis/scenicSpot";
import { useStore } from "vuex";
import { ref, reactive, computed, onBeforeMount } from "vue";
import { onBeforeRouteUpdate, useRoute } from "vue-router";

export default {
  name: "sightSeeing",
  components: {
    MainHeader,
    SpotList,
    SlideSpot,
  },
  setup() {
    const selectName = ref("");
    const category = ref("");
    const categoryName = ref("");
    const cardList = reactive([]);
    const scenicSpots = ref([]);
    const skip = ref(0);
    const top = ref(12);
    const isSlideLoading = ref(false);
    const isLoading = ref(false);
    const isProcessing = ref(false);
    const store = useStore();
    const currentArea = computed(() => store.state.currentArea);
    const route = useRoute();
    const area = ref(route.params.area);
    const fetchSlideSpot = async () => {
      isSlideLoading.value = true;
      let top = 50;
      let skip = Math.floor(Math.random() * 9) + 1;
      let count = top / 5;
      let tempObj = [];
      try {
        const { data } = await spotAPI.getSpot({
          area: currentArea.value.enName,
          select: "",
          filter: "",
          top: `${encodeURIComponent("$")}top=${top}&`,
          skip: `${encodeURIComponent("$")}skip=${skip}&`,
        });
        for (let i = 0; i < 5; i++) {
          tempObj.push({
            id: data[count - 1].ScenicSpotID,
            address: data[count - 1].Address
              ? data[count - 1].Address
              : data[count - 1].City,
            city: data[count - 1].City,
            picture: data[count - 1].Picture ? data[count - 1].Picture : {},
            name: data[count - 1].ScenicSpotName,
          });
          count += top / 5;
        }
        scenicSpots.value = tempObj;
      } catch (error) {
        console.log(error);
        scenicSpots.value = [];
      }
      isSlideLoading.value = false;
    };
    const fetchSpots = async () => {
      isLoading.value = true;
      let tempArr = [];
      const { data } = await spotAPI.getSpot({
        area: currentArea.value.enName,
        select: selectName.value
          ? `${encodeURIComponent("$")}select=${selectName.value}&`
          : "",
        filter: category.value
          ? `${encodeURIComponent("$")}filter=${encodeURIComponent(
              `contains(${category.value}, '${categoryName.value}')`
            )}&`
          : "",
        top: `${encodeURIComponent("$")}top=${top.value}&`,
        skip: `${encodeURIComponent("$")}skip=${skip.value}&`,
      });
      tempArr = data.map((card) => ({
        id: card.ScenicSpotID,
        address: card.Address ? card.Address : card.City,
        city: card.City,
        class1: card.Class1 ? card.Class1 : "",
        class2: card.Class2 ? card.Class2 : "",
        class3: card.Class3 ? card.Class3 : "",
        description: card.DescriptionDetail,
        openTime: card.OpenTime,
        phone: card.Phone,
        picture: card.Picture ? card.Picture : {},
        position: card.Position,
        name: card.ScenicSpotName,
      }));
      cardList.push(...tempArr);
      isLoading.value = false;
    };
    onBeforeMount(fetchSlideSpot);
    onBeforeMount(fetchSpots);
    const showMore = () => {
      isProcessing.value = true;
      skip.value += 12;
      setTimeout(() => {
        fetchSpots();
        isProcessing.value = false;
      }, 1000);
    };
    onBeforeRouteUpdate((to) => {
      const { area: newArea } = to.params;
      top.value = 12;
      skip.value = 0;
      cardList.splice(0, cardList.length);
      if (area.value !== newArea) {
        area.value = newArea;
        fetchSlideSpot();
        fetchSpots();
      }
    });
    return {
      cardList,
      scenicSpots,
      skip,
      top,
      isLoading,
      isSlideLoading,
      selectName,
      category,
      categoryName,
      isProcessing,
      currentArea,
      showMore,
    };
  },
  created() {
    this.$store.dispatch("GetAuthorizationHeader");
  },
};
</script>
